import Chord from "@/models/chords";
import Degree from "@/models/degree";
import FunctionalChord from "@/models/functional-chord";
import Settings, {
  checkSettingsValid,
  DEFAULT_SETTINGS,
} from "@/models/settings";
import { Module } from "vuex";

interface SettingsState {
  settingsList: Settings[] | undefined;
}

const DEFAULT_SETTINGS_LIST: Settings[] = [
  {
    ...DEFAULT_SETTINGS,
    name: "Major (common)",
    chords: [
      new FunctionalChord(Degree.I, Chord.MAJOR),
      new FunctionalChord(Degree.IV, Chord.MAJOR),
      new FunctionalChord(Degree.V, Chord.MAJOR),
      new FunctionalChord(Degree.VI, Chord.MINOR),
    ],
  },
  {
    ...DEFAULT_SETTINGS,
    name: "Major (diatonic)",
    chords: [
      new FunctionalChord(Degree.I, Chord.MAJOR),
      new FunctionalChord(Degree.II, Chord.MINOR),
      new FunctionalChord(Degree.III, Chord.MINOR),
      new FunctionalChord(Degree.IV, Chord.MAJOR),
      new FunctionalChord(Degree.V, Chord.MAJOR),
      new FunctionalChord(Degree.VI, Chord.MINOR),
      new FunctionalChord(Degree.VII, Chord.DIMINISHED),
    ],
  },
  {
    ...DEFAULT_SETTINGS,
    name: "Major (extended)",
    chords: [
      new FunctionalChord(Degree.I, Chord.MAJOR),
      new FunctionalChord(Degree.II, Chord.MINOR),
      new FunctionalChord(Degree.III, Chord.MINOR),
      new FunctionalChord(Degree.IV, Chord.MAJOR),
      new FunctionalChord(Degree.V, Chord.MAJOR),
      new FunctionalChord(Degree.VI, Chord.MINOR),
      new FunctionalChord(Degree.VII, Chord.DIMINISHED),
      new FunctionalChord(Degree.II_FLAT, Chord.MAJOR), // neapolitan
      new FunctionalChord(Degree.II, Chord.MAJOR), // establishes lydian mode, secondary dominant for V
      new FunctionalChord(Degree.III, Chord.MAJOR), // secondary dominant for vi
      new FunctionalChord(Degree.IV, Chord.MINOR), // substitute for V
      new FunctionalChord(Degree.VI, Chord.MAJOR), // secondary dominant for ii
      new FunctionalChord(Degree.VII_FLAT, Chord.MAJOR), // establishes mixolydian mode
    ],
  },
  {
    ...DEFAULT_SETTINGS,
    name: "Major (sevenths)",
    chords: [
      new FunctionalChord(Degree.I, Chord.MAJOR_SEVENTH),
      new FunctionalChord(Degree.II, Chord.MINOR_SEVENTH),
      new FunctionalChord(Degree.III, Chord.MINOR_SEVENTH),
      new FunctionalChord(Degree.IV, Chord.MAJOR_SEVENTH),
      new FunctionalChord(Degree.V, Chord.DOMINANT_SEVENTH),
      new FunctionalChord(Degree.VI, Chord.MINOR_SEVENTH),
    ],
  },
  {
    ...DEFAULT_SETTINGS,
    name: "Minor",
    chords: [
      new FunctionalChord(Degree.I, Chord.MINOR),
      new FunctionalChord(Degree.III_FLAT, Chord.MAJOR),
      new FunctionalChord(Degree.IV, Chord.MINOR),
      new FunctionalChord(Degree.V, Chord.MAJOR),
      new FunctionalChord(Degree.V, Chord.MINOR),
      new FunctionalChord(Degree.VI_FLAT, Chord.MAJOR),
      new FunctionalChord(Degree.VII_FLAT, Chord.MAJOR),
    ],
  },
];

export const SettingsModule: Module<SettingsState, any> = {
  namespaced: true,
  state: { settingsList: [] },
  getters: {
    setting(state) {
      return (settingsIndex: number) =>
        state.settingsList && state.settingsList[settingsIndex];
    },
  },
  mutations: {
    setSettings(state, payload: Settings[]) {
      state.settingsList = payload;
    },
    save(state, payload: { settings: Settings; index: number }) {
      if (!checkSettingsValid(payload.settings)) {
        throw new Error("Invalid Settings");
      }
      if (!state.settingsList) {
        throw new Error("Cannot save before load");
      }
      state.settingsList[payload.index] = payload.settings;
    },
    saveAs(state, payload: Settings) {
      if (!checkSettingsValid(payload)) {
        throw new Error("Invalid Settings");
      }
      if (!state.settingsList) {
        throw new Error("Cannot save before load");
      }
      state.settingsList.push(payload);
    },
  },
  actions: {
    load(context) {
      context.commit("settings/setSettings", DEFAULT_SETTINGS_LIST);
    },
    save(context, payload: { settings: Settings; index: number }) {
      context.commit("settings/save", payload);
    },
    saveAs(context, payload: Settings) {
      context.commit("settings/saveAs", payload);
    },
  },
};
