import Chord from "./chords";
import Degree from "./degree";
import Pitch from "./pitch";

class FunctionalChord {
  private readonly degree: Degree;
  private readonly chord: Chord;

  constructor(degree: Degree, chord: Chord) {
    this.degree = degree;
    this.chord = chord;
  }

  public build(tonic: Pitch): ReadonlyArray<Pitch> {
    const root = this.degree.calculate(tonic);
    return this.chord.build(root);
  }

  public isTonic(): boolean {
    return this.degree === Degree.I;
  }

  public getName(): string {
    return this.chord.getNameWithDegree(this.degree);
  }
}

export default FunctionalChord;
