import FunctionalChord from "./functional-chord";

export enum KeyChangeRate {
  ONE = 1,
  FIVE = 5,
  TEN = 10,
  NEVER = Number.POSITIVE_INFINITY,
}

export enum Range {
  LOWEST = 1,
  LOWER = 2,
  LOW = 3,
  CENTER = 4,
  HIGH = 5,
  HIGHER = 6,
  HIGHEEST = 7,
}

export enum Speed {
  SLOW = 2000,
  MEDIUM = 1000,
  FAST = 500,
}

export const DEFAULT_SETTINGS: Settings = {
  name: "",
  chords: [],
  keyChangeRate: KeyChangeRate.NEVER,
  ranges: [Range.LOW, Range.CENTER, Range.HIGH],
  speed: Speed.MEDIUM,
  progressionLength: 4,
  isFirstNoteAlwaysTonic: true,
  isHardMode: false,
};

export function checkSettingsValid(settings: Settings): string | undefined {
  if (
    settings.isFirstNoteAlwaysTonic &&
    settings.chords.every((chord) => !chord.isTonic())
  ) {
    return "First note should be tonic but no tonic chords were chosen.";
  }
  return undefined;
}

export default interface Settings {
  name: string;
  chords: ReadonlyArray<FunctionalChord>;
  keyChangeRate: KeyChangeRate;
  ranges: ReadonlyArray<Range>;
  speed: Speed;
  progressionLength: number;
  isFirstNoteAlwaysTonic: boolean;
  isHardMode: boolean;
}
