import { DEGREES_PER_OCTAVE, modPositive } from "./utils";

class Letter {
  private readonly index: number;
  private readonly chromaticOffset: number;
  private readonly name: string;

  public static readonly A: Letter = new Letter(0, "A");
  public static readonly B: Letter = new Letter(2, "B");
  public static readonly C: Letter = new Letter(3, "C");
  public static readonly D: Letter = new Letter(5, "D");
  public static readonly E: Letter = new Letter(7, "E");
  public static readonly F: Letter = new Letter(8, "F");
  public static readonly G: Letter = new Letter(10, "G");

  private static readonly notes: Letter[] = [];

  private constructor(chromaticOffset: number, name: string) {
    this.index = Letter.notes.length;
    this.chromaticOffset = chromaticOffset;
    this.name = name;
    Letter.notes.push(this);
  }

  public getChromaticOffset(): number {
    return this.chromaticOffset;
  }

  public getName(): string {
    return this.name;
  }

  public shift(diatonicOffset: number): Letter {
    if (DEGREES_PER_OCTAVE > Math.abs(diatonicOffset)) {
      throw new RangeError(`Invalid diatonic offset: ${diatonicOffset}`);
    }
    return Letter.notes[
      modPositive(this.index + diatonicOffset, DEGREES_PER_OCTAVE)
    ];
  }
}

export default Letter;
